import React, { useState, useEffect } from "react";
import Head from "next/head";

import { Header } from "../components/Header";
import { Info } from "../components/Info";
import { Footer } from "../components/Footer";

const myAge = new Date().getFullYear() - 1988;

const calcSecondsFromBd = () => {
  const now = new Date();
  const currYear = now.getFullYear();
  const bdCurrYear = new Date(`${currYear}/01/12 10:50:00`);
  return Math.trunc((now.getTime() - bdCurrYear.getTime()) / 1000);
};

const Index: React.SFC = () => {
  const [secondsFromBd, setSecondsFromBd] = useState(calcSecondsFromBd());

  useEffect(() => {
    const interval = setInterval(() => {
      setSecondsFromBd(calcSecondsFromBd);
    }, 1000);
    return () => clearInterval(interval);
  }, []);

  return (
    <>
      <Head>
        <title>Dmitriy Gurinenko | Frontend developer | React, Vue</title>
        <meta charSet="utf-8" />
        <meta
          name="viewport"
          content="initial-scale=1.0, width=device-width"
          key="viewport"
        />
        <meta
          name="description"
          content="Dmitriy Gurinenko CV"
          key="description"
        />
        <link rel="icon" type="image/x-icon" href="/static/favicon.ico" />
      </Head>

      <Header age={myAge} seconds={secondsFromBd} />
      <Info />
      <Footer />

      <style global jsx>{`
        html {
          font-family: "Quicksand", "Source Sans Pro", -apple-system,
            BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial,
            sans-serif; /* 1 */
          font-size: 16px;
          word-spacing: 1px;
          -ms-text-size-adjust: 100%;
          -webkit-text-size-adjust: 100%;
          -moz-osx-font-smoothing: grayscale;
          -webkit-font-smoothing: antialiased;
          box-sizing: border-box;
          background-color: #35495e;
        }

        *,
        *:before,
        *:after {
          box-sizing: border-box;
          margin: 0;
        }
        a {
          text-decoration: none;
          color: inherit;
        }
      `}</style>
    </>
  );
};

export default Index;
